@import url(https://fonts.googleapis.com/css?family=Lato|Lora);
@import url(https://fonts.googleapis.com/css?family=Lato|Lora);
@import url(https://fonts.googleapis.com/css?family=Lato|Lora);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  display: grid;
  min-height: 100%;
}

body {
  display: grid;
  text-transform: uppercase;
  min-width: 1600px;
}

.container {
  width: 80%;
  margin: 0 auto;
}

header{display:flex;top:0;height:170px}header a{color:white;margin:20px}

body{background:#231F20;font-family:"Lato",sans-serif}.grid{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:10px;grid-auto-rows:100px}.item{position:relative}.cup{background:#231F20;border:2px solid #fff;position:absolute}.handle{background:transparent;border:2px solid #fff;border-radius:60% 30%;height:20px;left:90%;position:absolute;top:10%;width:15px;z-index:-1}.cup-tall{width:55px;height:40px;border-radius:0 0 5rem 5rem;margin:30px}.cup-grande{width:70px;height:50px;border-radius:0 0 5rem 5rem;margin:23px}.cup-venti{width:60px;height:90px;border-radius:10% 10% 40% 40%;margin:3px 25px}.info{margin-left:150px;margin-top:30px}h1{color:#fff;font-size:1em;letter-spacing:3px}p{color:#fff;font-family:"Lora",serif;text-transform:none;font-size:0.7em;letter-spacing:0.5px;margin-top:-5px}

